.MediasCard {
  margin: 0;
  padding: 0; }
  .MediasCard .Content {
    border-radius: 8px;
    margin: 6px;
    padding: 16px;
    font-weight: bold;
    background-color: white;
    color: #00213b; }
    .MediasCard .Content .TopCard {
      margin: 0;
      padding: 0;
      margin-bottom: 12px;
      height: 7em;
      position: relative;
      overflow: hidden;
      max-height: 4.1em; }
      .MediasCard .Content .TopCard .Title {
        font-size: 1.1em;
        line-height: 1.3em;
        max-height: 3.9em; }
      .MediasCard .Content .TopCard .Title:after {
        content: "";
        text-align: right;
        position: absolute;
        bottom: 0;
        right: 0;
        width: 70%;
        height: 1.2em;
        background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(50%, white));
        background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), white 50%);
        background: -o-linear-gradient(left, rgba(255, 255, 255, 0), white 50%);
        background: linear-gradient(to right, rgba(255, 255, 255, 0), white 50%); }
    .MediasCard .Content .BottomCard {
      padding: 0;
      margin: 0;
      height: 2em; }
      .MediasCard .Content .BottomCard .Source {
        font-weight: bold;
        text-align: left;
        height: 7em;
        position: relative;
        overflow: hidden;
        max-height: 1.3em; }
        .MediasCard .Content .BottomCard .Source .TitleSource {
          font-size: 0.9em;
          line-height: 1.3em;
          max-height: 1.3em; }
        .MediasCard .Content .BottomCard .Source .TitleSource:after {
          content: "";
          text-align: right;
          position: absolute;
          bottom: 0;
          right: 0;
          width: 30%;
          height: 1.2em;
          background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(50%, white));
          background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), white 50%);
          background: -o-linear-gradient(left, rgba(255, 255, 255, 0), white 50%);
          background: linear-gradient(to right, rgba(255, 255, 255, 0), white 50%); }
      .MediasCard .Content .BottomCard .Date {
        font-size: 0.8rem;
        font-weight: normal; }
      .MediasCard .Content .BottomCard .ButtonPosition {
        position: relative; }
        .MediasCard .Content .BottomCard .ButtonPosition .Button {
          position: absolute;
          top: 20%;
          width: 6rem;
          background-color: #3877ba;
          color: white; }



