.PersonCard {
  background-color: white;
  border-radius: 8px;
  text-align: center;
  min-height: 150px;
  padding: 8px;
  margin: 3px; }
  .PersonCard .Title {
    color: #4c6f8b;
    font-style: italic;
    font-size: 0.9rem; }
  .PersonCard .Logo {
    height: 60px;
    overflow: hidden; }
  .PersonCard .Name {
    color: #003259;
    font-size: 1.1rem;
    font-weight: bold; }
  .PersonCard .Role {
    color: #4c6f8b;
    font-size: 0.9rem; }
  .PersonCard .Email {
    color: #003259; }
    .PersonCard .Email a, .PersonCard .Email a:hover, .PersonCard .Email a:focus {
      color: #003259;
      text-decoration: none;
      position: relative; }
    .PersonCard .Email a:after {
      content: '';
      width: 100%;
      position: absolute;
      left: 0;
      bottom: -2px;
      border-width: 0 0 2px;
      border-style: solid;
      border-color: #3778bb; }
  .PersonCard .Email::before {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f0e0";
    padding-right: 8px;
    color: #3778bb; }
  .PersonCard .Button {
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 80%;
    font-size: 1rem; }



