.Content {
  padding: 0 16px;
  color: #ebeef0;
  height: 100%; }
  .Content H2 {
    padding-left: 24px;
    font-size: 1.5rem;
    font-weight: bolder; }
  .Content H2 > SPAN {
    display: inline-block; }
  .Content H2 > SPAN:first-child {
    padding-right: 12px; }
  .Content H2 > SPAN:last-child {
    padding-left: 12px; }
  .Content HR {
    border-bottom: 2px solid #4c6f8b;
    margin: 24px; }
  .Content UL {
    height: 80%;
    overflow-y: scroll;
    padding-left: 0;
    padding-right: 16px; }
  .Content UL::-webkit-scrollbar {
    width: 14px;
    height: 14px;
    -webkit-border-radius: 10px;
    background-color: #3778bb; }
  .Content UL::-webkit-scrollbar-thumb {
    border: solid 0 transparent;
    border-right-width: 1px;
    border-left-width: 1px;
    -webkit-border-radius: 10px;
    -webkit-box-shadow: inset 0 0 0 1px white, inset 0 0 0 6px White; }
  .Content LI {
    list-style-type: none; }
    .Content LI .Item {
      padding-left: 40px; }
  .Content .Logo {
    display: inline-block;
    width: 24px; }
  .Content .Title {
    margin-top: 12px;
    font-weight: bold; }
  .Content .Description {
    color: #ccd6de;
    margin-bottom: 12px;
    padding-top: 8px;
    padding-left: 24px; }
  .Content .Switch {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg); }
  .Content .IsTarget {
    border: 1px solid #4c6f8b;
    border-left: 8px solid #4c6f8b; }



